import React from "react"
import { Flex, Icon, Link, Tooltip, Box } from "@chakra-ui/react"
import { EditIcon, ChatIcon, CopyIcon } from "@chakra-ui/icons"
import useCopyToClipboard from "../../hooks/useCopyToClipboard"
import useApiVersioning from "../../hooks/useApiVersioning"

const Feedback = ({ location }) => {
  const copyToClipboard = useCopyToClipboard()
  //remove leading and trailing slashes from the current url
  const url = location.pathname.replace(/(^\/)|(\/$)/g, "")
  const copyLink = location.href

  const { selectedApiVersion } = useApiVersioning()
  const repoRoot = `https://github.com/${process.env.GATSBY_DOCUMENTATION_REPO}/edit/${selectedApiVersion?.value}/${process.env.GATSBY_DOCUMENTATION_FOLDER}/${url}.md`

  return (
    <Box mt={5}>
      <Box fontWeight={600} fontSize={20}>
        Leave Feedback
      </Box>
      <Flex gap="4" justifyContent="flex-start" w="100%" mt={4}>
        <Tooltip label="Copy Link" openDelay={500} bg="rlGrey20">
          <Link
            onClick={() => {
              copyToClipboard(copyLink)
            }}
          >
            <Icon as={CopyIcon} color="rlFeedbackIconGrey" w={5} h={5} />
          </Link>
        </Tooltip>
        <Tooltip label="Edit this page" openDelay={500} bg="rlGrey20">
          <Link target="_blank" href={repoRoot}>
            <Icon as={EditIcon} color="rlFeedbackIconGrey" w={5} h={5} />
          </Link>
        </Tooltip>
        <Tooltip label="Log an issue" openDelay={500} bg="rlGrey20">
          <Link
            target="_blank"
            href={`https://github.com/${process.env.GATSBY_DOCUMENTATION_REPO}/issues/new`}
          >
            <Icon as={ChatIcon} color="rlFeedbackIconGrey" w={5} h={5} />
          </Link>
        </Tooltip>
      </Flex>
    </Box>
  )
}

export default Feedback
