import { Box, Flex, VStack } from "@chakra-ui/react"
import Menu from "../components/menu/menu"
import renderAst from "../utils/render-ast"
import Toc from "../components/toc/toc"
import Feedback from "../components/feedback/feedback"
import * as React from "react"
import { useEffect, useMemo, useState } from "react"
import { getTocData } from "../utils/get-toc-data"

const DocumentationPage = ({ data, location }) => {
  const [offset, setOffset] = useState(0)

  useEffect(() => {
    const onScroll = () => setOffset(window.pageYOffset)
    window.addEventListener("scroll", onScroll, { passive: true })
    return () => window.removeEventListener("scroll", onScroll)
  }, [])

  const tocData = useMemo(() => {
    return getTocData(data?.toc?.htmlAst)
  }, [data])

  const page = data.pageContent

  return (
    <Box p={{ base: 4, md: 8, lg: 10 }} display="flex">
      <Flex w="100%" pt={{ base: "none", md: 16 }} display={{ md: "flex" }}>
        <Box
          pt={5}
          mb={{ base: 10, md: "none" }}
          w={380}
          mr={{ base: "none", md: "none", lg: "none", xl: 10 }}
        >
          <Menu content={tocData} rootTitle={data.toc} location={location} />
        </Box>
        <Box
          flex="1"
          alignItems="center"
          justifyContent="center"
          px={{ md: "0" }}
          pt={5}
          maxW="42rem"
        >
          <Box>{renderAst(page.htmlAst)}</Box>
        </Box>
        <Box
          display={{ base: "none", md: "none", lg: "flex", xl: "flex" }}
          pt={5}
          ml="3.5em"
          w={{ lg: "12em", xl: "18em" }}
        >
          <VStack
            position="fixed"
            overflow="hidden"
            zIndex={1}
            top={offset > 150 ? 104 : ""}
          >
            <Box>
              <Flex overflow="auto" maxW="18em" maxH="60vh">
                <Toc data={data.pageContent} location={location} />
              </Flex>
              <Feedback location={location} />
            </Box>
          </VStack>
        </Box>
      </Flex>
    </Box>
  )
}

export default DocumentationPage
