import * as React from "react"
import Layout from "../components/layout"
import Seo from "../components/seo"
import { graphql } from "gatsby"

import DocumentationPage from "./documentation-page"
import HomePage from "./home-page"

const BasePage = ({ data, location }) => {
  const page = data.pageContent
  const pageType = page.fileAbsolutePath.includes(
    `/${process.env.GATSBY_HOMEPAGES_FOLDER}/`
  )
    ? "homePage"
    : "docPage"

  return (
    <Layout tocContents={data.rootToc} location={location}>
      <Seo title={page.frontmatter.title} />
      {pageType === "docPage" ? (
        <DocumentationPage data={data} location={location} />
      ) : (
        <HomePage page={page} />
      )}
    </Layout>
  )
}

export const query = graphql`
  query ($absolutePath: String!, $tocRegex: String!, $rootTocRegex: String!) {
    pageContent: markdownRemark(fileAbsolutePath: { eq: $absolutePath }) {
      html
      htmlAst
      fileAbsolutePath
      frontmatter {
        title
      }
      headings {
        id
        value
        depth
      }
    }
    toc: markdownRemark(fileAbsolutePath: { regex: $tocRegex }) {
      html
      htmlAst
      fileAbsolutePath

      frontmatter {
        title
      }
    }
    rootToc: markdownRemark(fileAbsolutePath: { regex: $rootTocRegex }) {
      fileAbsolutePath
      frontmatter {
        title
      }
      htmlAst
    }
  }
`

export default BasePage
